




















import Vue from 'vue'

import { IUser } from './store'

export default Vue.extend({
  name: 'Users',

  props: {
    users: {
      type: Array as () => IUser[],
      required: true
    },

    loading: {
      type: Boolean,
      required: true
    },

    loaded: {
      type: Boolean,
      required: true
    },

    touched: {
      type: Boolean,
      required: true
    },
  },

  methods: {
    fetchUsers() {
      this.$emit('fetchUsers')
    },

    clearUsers() {
      this.$emit('clearUsers')
    },
  }
})
